<script>
import { ref, reactive, onMounted } from "vue"
import { useRouter } from "vue-router"
import { Toast } from "../../../components/global/swal"
// call api
import { apiAddFavCaretaker, apiDeleteFavCaretaker } from "@/api/api"

export default {
  props: {
    item: Object,
  },
  setup(props) {
    const router = useRouter()
    const data = reactive(props.item)
    const addFav = async (val) => {
      if (localStorage.getItem("is_Login") != '1') {
        Toast("登入後才可使用最愛功能")
        return
      }
      if (data.isFavorite) {
        let msg = await apiDeleteFavCaretaker({ id: data.id })
        Toast("已移除最愛")
        data.isFavorite = !data.isFavorite
      } else {
        let msg = await apiAddFavCaretaker({ id: data.id })
        Toast("已加入最愛")
        data.isFavorite = !data.isFavorite
      }
    }
    let starClass = ref([])
    let star = Math.round(data.star)
    for (let i = 0; i < star; i++) {
      starClass.value.push("fas fa-star active")
    }
    for (let i = 0; i < 5 - star; i++) {
      starClass.value.push("fas fa-star")
    }

    const goto = (val) => {
      router.push({ path: val })
    }

    return {
      data,
      goto,
      addFav,
      starClass,
    }
  },
}
</script>

<template>
  <div class="card">
    <div class="card-header" @click="goto(`/caregiver/info/${data.id}`)">
      <a class="card-link">
        <img
          :src="data.avatarImgUrl"
          class="card-img"
          v-if="data.avatarImgUrl"
          onerror="this.onerror=null; this.src='https://fakeimg.pl/101x134/'"
        />
        <img
          src="@/assets/image/noavatar.jpg"
          class="card-img"
          v-if="!data.avatarImgUrl"
        />
      </a>
    </div>
    <div class="card-body">
      <a class="card-link" @click="goto(`/caregiver/info/${data.id}`)">
        <div class="card-title">
          {{ data.name
          }}<i
            :class="data.gender == '女性' ? 'far fa-venus' : 'far fa-mars'"
          ></i>
        </div>
        <div class="card-info">
          {{ data.age }}歲/{{ data.height }}cm/{{ data.weight }}kg 年資{{
            data.Seniority
          }}
        </div>
        <div class="card-license">{{ data.license?.join("、") }}</div>
        <div class="card-text" v-if="data.experience?.length > 0">
          照顧經驗：{{ data.experience.join("、") }}
        </div>
        <div class="card-text" v-if="data.experience?.length == 0">
          照顧經驗：無
        </div>
      </a>
      <div class="card-rating">
        <span class="average">{{ data.star }}</span
        ><span class="star"
          ><i :class="starClass[0]"></i><i :class="starClass[1]"></i
          ><i :class="starClass[2]"></i><i :class="starClass[3]"></i
          ><i :class="starClass[4]"></i></span
        ><span class="total">({{ data.commentAmount }})</span>
      </div>
      <a
        :class="
          data.isFavorite == 0 ? 'btn btn-favorite' : 'btn btn-favorite favor'
        "
        ><i class="far fa-heart" @click="addFav"></i
      ></a>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.favor {
  i {
    color: #ff345f;
  }

  .far {
    font-weight: 900;
  }
}
</style>
