<script>
import { ref, onMounted, onUnmounted } from 'vue'
import Header from "../../components/global/header/HeaderArea.vue"
import Footer from "../../components/global/footer/FooterArea.vue"
import NoData from "../../components/global/NoData.vue"
import CaregiverCard from "../caregiver/components/CaregiverCard.vue"
import { apiGetCaretakerList } from "@/api/api"
import { isBetweenBottom } from "@/utils/helper";

export default {
  setup() {
    const favCaregiver = ref([])
    const page = ref(1)
    let totalData = 0
    let getApiTimer = null

    onMounted(async () => {
      favCaregiver.value = await apiGetCaretakerList({ page: page.value, limit: 10, isFavorite: 1 })
      totalData = favCaregiver.value.data.total
      favCaregiver.value = favCaregiver.value.data.data
      // console.log(favCaregiver.value)

      //每0.5S監測是否在底部
      getApiTimer = setInterval(() => {
        if (!isBetweenBottom()) return
        // 超過上限不做GET
        if (favCaregiver.value.length < totalData) {
          handleScroll();
        }
      }, 500)
    })

    //滾動載入資料
    const handleScroll = async () => {
      page.value += 1;
      let newData = ref()
      // console.log(page.value)
      newData.value = await apiGetCaretakerList({ page: page.value, limit: 10, isFavorite: 1 })
      favCaregiver.value = favCaregiver.value.concat(newData.value.data.data);
    };
    //取消監控
    onUnmounted(() => clearInterval(getApiTimer))


    return { favCaregiver };
  },
  components: {
    Header, Footer, CaregiverCard, NoData
  },
};
</script>

<template>
  <div class="favCaregiver">
    <Header title="最愛看護"  backToPath="/member"/>
    <div class="caregiver-outer">
      <NoData v-if="favCaregiver.length == 0" />
      <div>
        <CaregiverCard v-for="item in favCaregiver" :item="item" :key="item.id" />
      </div>
    </div>
    <Footer />
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.favCaregiver {
  margin-top: $header-height;
  margin-bottom: $footer-height;
}
</style>